// @ts-nocheck
import "./App.css";
import React, { useEffect, useState, useContext, useRef } from "react";

//ui
import {
  Button, Select, MenuItem, TextField, Avatar, IconButton, makeStyles, createStyles, Card, CardActionArea
} from "@material-ui/core";

//logic
import io from "socket.io-client";
import { DAppClient, TezosOperationType } from "@airgap/beacon-sdk";
import { TezosToolkit } from '@taquito/taquito'
import {
  BeaconWallet,
} from '@taquito/beacon-wallet'

import { FirebaseContext } from "./firebaseContext";
import { useSnackbar } from "notistack";
import axios from 'axios';
import loadingGif from "./assets/green-loader.gif"
import share from "./assets/share_icon.png"


const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      display: 'flex',
    },
    size: {
      width: theme.spacing(15),
      height: theme.spacing(15),
    },
    preview: {
      width: theme.spacing(40),
      height: theme.spacing(40),
    },
    containedSuccess: {
      backgroundColor: theme.palette.success.main,
      color: "#fff",
      "&:hover": {
        backgroundColor: theme.palette.success.dark
      }
    },
    disabled: {}
  }),
);

const Tezos = new TezosToolkit('https://mainnet.api.tez.ie')
const wallet = new BeaconWallet({
  name: 'hicetnunc.xyz',
  preferblackNetwork: 'mainnet',
})
Tezos.setWalletProvider(wallet)
const socketURL =
  window.location.hostname === "localhost"
    ? "ws://localhost:8000"
    : "wss://network5backend.herokuapp.com";

const socket = io(socketURL, { transports: ["websocket"] });
const dAppClient = new DAppClient({ name: "Beacon Docs" });
const versionNames = ["0", "v1.0", "v2.0", "v3.0", "v4.0", "v5.0"];

function App() {
  const classes = useStyles();
  const [activeAccount, setActiveAccount] = useState();
  const [synced, setSynced] = useState("sync");
  const [showUnsync, setShowUnsync] = useState(false);

  const { getRequest, setRequest, getRequests, setAnswer } =
    useContext(FirebaseContext);

  const { enqueueSnackbar } = useSnackbar();
  const [requests, setRequests] = useState([]);
  const [answers, setAnswers] = useState([]);
  const [version] = useState(5);
  const [view, setView] = useState("answers");
  const [requestInput, setRequestInput] = useState();
  const [prizeInput] = useState(0.003);
  const [selectedRequest, setSelectedRequest] = useState();
  const [imageInput, setImageInput] = useState();
  const [image, setImage] = useState();
  const [messageInput, setMessageInput] = useState();
  const [resultImages, setResultImages] = useState();
  const [transactionWait, setTransactionWait] = useState();
  const isMobile = window.innerWidth <= 950;
  const [hoverText, setHoverText] = useState("");
  const [selectedHoverText, setSelectedHoverText] = useState("");
  const [hoveringRequest, setHoveringRequest] = useState(0);
  const selectedReqRef = useRef();
  const selectedAnswerRef = useRef();
  const [selectedAnswer, setSelectedAnswer] = useState();
  const [answerOwnerDomains, setAnswerOwnerDomains] = useState([]);


  useEffect(() => {
    async function fetchDomains() {
      let tempDomains = new Array(answers.length);;
      for (let i = 0; i < answers.length; i++) {

        let domain;
        await fetch('https://api.tezos.domains/graphql', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            query: `
              {
                reverseRecord(address: "`+ answers[i].owner + `"){owner domain{name}}
              }
              `,
            variables: {
            },
          }
          ),
        })
          .then((res) => res.json())
          .then((result) => {
            if (result.data.reverseRecord) {
              domain = result.data.reverseRecord.domain.name;
              tempDomains[i] = domain;
            }
          });
      }
      setAnswerOwnerDomains(tempDomains);
    }
    fetchDomains()
  }, [answers]);

  useEffect(() => {
    async function getProfiles() {
      let result = await getRequests();
      console.log(result)
      setRequests(result);

      const url1 = new URL(window.location.href);
      let sharedReqTimestamp = url1.searchParams.get('request');

      if (result.length > 0) {
        let defaultRequest = result[0];
        if (sharedReqTimestamp) {
          console.log(sharedReqTimestamp)
          const sharedReq = result.find(({ timestamp }) => timestamp === parseInt(sharedReqTimestamp));
          defaultRequest = sharedReq;
          console.log(sharedReq)
        }

        setSelectedRequest(defaultRequest);

        let tempAnswers = await getRequest(defaultRequest.timestamp);
        setAnswers(tempAnswers);
        let tempTotalBounty = 0;
        for (let i = 0; i < tempAnswers.length; i++) {
          if (tempAnswers[i].bounty && tempAnswers[i].bounty > 0)
            tempTotalBounty++;
        }

        setSelectedHoverText(`Responses ${tempAnswers.length} Rewards ${tempTotalBounty} Bounty ${tempTotalBounty * 0.003}`)
        selectedReqRef.current.scrollIntoView({ behavior: 'smooth', inline: "center", block: "nearest" })


        setSelectedAnswer(parseInt(url1.searchParams.get('answer')))

        setTimeout(function () {
          if (selectedAnswerRef.current)
            selectedAnswerRef.current.scrollIntoView({ behavior: 'smooth', block: "center" })
        }, 2000);


      }
    }
    getProfiles();
  }, [getRequests, getRequest]);

  useEffect(() => {
    const onRequestChange = (data) => {
      setRequests(
        requests
          .concat(data)
          .sort((a, b) => parseFloat(b.timestamp) - parseFloat(a.timestamp))
      );
    };

    socket.on("request", onRequestChange);

    return () => {
      socket.off("request", onRequestChange);
    };
  }, [requests]);

  useEffect(() => {
    async function getAcc() {
      setActiveAccount(await dAppClient.getActiveAccount());
      if (activeAccount) {
        setSynced(
          activeAccount.address.slice(0, 6) +
          "..." +
          activeAccount.address.slice(32, 36)
        );
        setShowUnsync(true);
      } else {
        setSynced("sync");
        setShowUnsync(false);
      }
    }
    getAcc();
  }, [activeAccount]);

  async function unsync() {
    setActiveAccount(await dAppClient.getActiveAccount());
    if (activeAccount) {
      // User already has account connected, everything is ready
      dAppClient.clearActiveAccount().then(async () => {
        setActiveAccount(await dAppClient.getActiveAccount());
        setSynced("sync");
        setShowUnsync(false);
      });
    }
  }

  async function sync() {
    setActiveAccount(await dAppClient.getActiveAccount());
    //Already connected
    if (activeAccount) {
      setSynced(activeAccount.address);
      setShowUnsync(true);

      return activeAccount;
    }
    // The user is not synced yet
    else {
      try {
        console.log("Requesting permissions...");
        const permissions = await dAppClient.requestPermissions();
        setActiveAccount(await dAppClient.getActiveAccount());
        console.log("Got permissions:", permissions.address);
        setSynced(permissions.address);
        setShowUnsync(true);
      } catch (error) {
        console.log("Got error:", error);
      }
    }
  }

  const getSearchedUnsplashImages = async (text: string) =>
    await axios.get('https://api.unsplash.com/search/photos?per_page=15', {
      params: { query: text },
      headers: {
        authorization: 'Client-ID oSwI9b8UY-1ZV8yBYyH871XgVv8vCBT2jwj0xIol0hM'
      }
    });

  const handleRequestInput = (event: React.ChangeEvent<HTMLInputElement>) => {
    setRequestInput(event.target.value.slice(0, 250));
  };

  const handleImageInput = (event: React.ChangeEvent<HTMLInputElement>) => {
    setImageInput(event.target.value);
  };

  const handleMessageInput = (event: React.ChangeEvent<HTMLInputElement>) => {
    setMessageInput(event.target.value.slice(0, 250));
  };

  const searchImages = async (input) => {
    const response = await getSearchedUnsplashImages(input);
    console.log(response);

    const imageDataWanted = response.data.results.map(
      ({ urls, alt_description, id }: IResponseDataUnsplash) => {
        const { regular, thumb } = urls;
        return {
          alt: alt_description,
          imageLink: regular,
          thumbnailLink: thumb,
          id: id
        };
      }
    );

    setResultImages(imageDataWanted);

  };

  const onKeyPressImageSearch = (event: React.KeyboardEvent<HTMLInputElement>) => {
    if (event.key === 'Enter') {
      searchImages(event.target.value)
    }
  };

  return (

    <div>
      <div
        className="top-left"
        style={{
          fontSize: "1em",
          display: "flex",
          alignItems: "center",
          marginLeft: 6,
          width: "80%",

        }}
      >
        <div
          style={{
            fontSize: "1.5em",
            fontFamily: "Tangerine",
            marginRight: 10,
            marginTop: 6
          }}
        >
          <b>Network </b>
        </div>
        &nbsp;
        <Select
          value={version}
          label="version"
          onChange={(e) => {
            let target;
            if (e.target.value === 1) {
              target = "https://network1.cc/";
            } else if (e.target.value === 2) {
              target = "https://adventurepizza.github.io/Network2/";
            }
            else if (e.target.value === 3) {
              target = "https://adventurepizza.github.io/Network3/";
            }
            else if (e.target.value === 4) {
              target = "https://adventurepizza.github.io/Network4/";
            }
            window.location.href = target;
            return null;
          }}
        >
          <MenuItem value={1}> {versionNames[1]}</MenuItem>
          <MenuItem value={2}> {versionNames[2]}</MenuItem>
          <MenuItem value={3}> {versionNames[3]}</MenuItem>
          <MenuItem value={4}> {versionNames[4]}</MenuItem>
          <MenuItem value={5}> {versionNames[5]}</MenuItem>

        </Select>
        &nbsp; ðŸ“ 
        {!isMobile &&
          <div style={{ textAlign: "center", width: "100%" }}>
            <b> Try Network, Request from the Network, Answer Requests, Reward Responses  </b>
          </div>}
      </div>

      <div
        className="top-right"
        style={{ position: "absolute", display: "flex", alignItems: "center", backgroundColor: "white", margin: 6 }}
      >
        {showUnsync && (
          <Button
            size={"small"}
            title={"unsync"}
            onClick={() => {
              unsync();
            }}
          >
            <u>unsync</u>{" "}
          </Button>
        )}

        {showUnsync && <div> | </div>}
        <Button
          title={"sync"}
          size={"small"}
          onClick={async () => {
            await sync();
          }}
        >
          <u>{synced}</u>{" "}
        </Button>

      </div>
      {isMobile &&
        <div style={{ textAlign: "center", width: "100%", marginTop: 10 }}>
          <b> Try Network, Request from the Network, Answer Requests, Reward Responses ðŸ§§ </b>
        </div>}

      <div style={{ fontSize: "0.9em", marginTop: 13, marginLeft: 13 }}>
        <b>Requests</b>
      </div>
      <div
        style={{
          display: "flex",
          width: "100%",
          marginLeft: "auto",
          marginRight: "auto",
          overflowX: "scroll",
        }}
      >

        <div style={{ textAlign: "center", margin: 2 }}>

          <Button
            onClick={async () => {
              //add req
              setView("+request");
            }}
            style={{ height: "100%", border: "solid 2px " }}
          >
            add new request
          </Button>
        </div>


        {requests &&
          requests.map((request) => (
            <div
              key={request.timestamp}
              style={{ textAlign: "center", margin: 2 }}
              onMouseEnter={
                async () => {
                  //fetch and set hover text
                  if (selectedRequest !== request.timestamp) {
                    let tempAnswers = await getRequest(request.timestamp);
                    let tempTotalBounty = 0;
                    for (let i = 0; i < tempAnswers.length; i++) {
                      if (tempAnswers[i].bounty && tempAnswers[i].bounty > 0)
                        tempTotalBounty++;
                    }

                    setHoverText(`Responses ${tempAnswers.length} Rewards ${tempTotalBounty} Bounty ${tempTotalBounty * request.prize}`)
                    setHoveringRequest(request.timestamp)
                  }
                }
              }
              onMouseLeave={() => {
                setHoveringRequest(0)
              }}
            >

              <Card
                ref={(selectedRequest && request.timestamp === selectedRequest.timestamp) ? selectedReqRef : null}
                raised={selectedRequest && request.timestamp === selectedRequest.timestamp}
                onClick={async () => {
                  //open answers
                  //console.log(await getRequest(request.timestamp))

                  setAnswers(await getRequest(request.timestamp));
                  setView("answers");
                  setSelectedRequest(request);
                  if (!isMobile) {
                    setSelectedHoverText(hoverText)
                  }
                  else {
                    let tempAnswers = await getRequest(request.timestamp);
                    let tempTotalBounty = 0;
                    for (let i = 0; i < tempAnswers.length; i++) {
                      if (tempAnswers[i].bounty && tempAnswers[i].bounty > 0)
                        tempTotalBounty++;
                    }

                    setSelectedHoverText(`Responses ${tempAnswers.length} Rewards ${tempTotalBounty} Bounty ${tempTotalBounty * request.prize}`)

                  }
                  selectedReqRef.current.scrollIntoView({ behavior: 'smooth', inline: "center", block: "nearest" })
                }}
                style={{ width: 320, height: 85, margin: 2 }}
              >
                <CardActionArea
                  style={{ width: "100%", height: "100%" }}
                >
                  <div style={{ position: "relative", height: "100%" }}>
                    <div style={{ fontSize: "1.2em", paddingTop: 10 }}>
                      <b>{request.request}</b>
                    </div>
                    <div style={{ fontSize: "0.9em", position: "absolute", bottom: 10, width: "100%" }}>
                      {(request.timestamp === hoveringRequest) && !(selectedRequest && request.timestamp === selectedRequest.timestamp) && <b> {hoverText}</b>}
                      {(selectedRequest && request.timestamp === selectedRequest.timestamp) && <b> {selectedHoverText}</b>}

                    </div>
                    <div style={{ position: "absolute", right: 0, bottom: 0 }}>
                      {((request.timestamp === hoveringRequest) || (selectedRequest && request.timestamp === selectedRequest.timestamp)) &&
                        <Button
                          onClick={async () => {
                            console.log(window.location.href)

                            //share
                            navigator.clipboard.writeText(`https://asknetwork.xyz/?request=${selectedRequest.timestamp}`);
                            enqueueSnackbar("Link copied !", {
                              variant: "success",
                            });
                          }}
                        >
                          <img src={share} alt="share" width="25" />
                        </Button>}
                    </div>
                  </div>
                </CardActionArea>
              </Card>

            </div>
          ))}
      </div>

      {view === "answers" &&
        <>
          <div style={{ fontSize: "0.9em", marginTop: 13, marginLeft: 13, overflowY: "auto" }}>
            <b>Answers</b>
          </div>
          <div
            style={{
              display: "grid",
              width: isMobile ? "100%" : 400,
              marginLeft: "auto",
              marginRight: "auto",
              overflowX: "scroll",
            }}
          >
            {selectedRequest &&
              <>
                <div style={{ textAlign: "center", margin: 6 }}>
                  <Button
                    variant="contained"
                    color="primary"
                    onClick={async () => {
                      //add answer
                      setView("+answer");
                    }}
                  >
                    ADD
                  </Button>
                </div>
              </>
            }
            {answers &&
              answers.map((answer, index) => (
                <div
                  key={answer.timestamp}
                  style={{ textAlign: "center", margin: 6 }}
                >

                  <Card
                    ref={(answer.timestamp === selectedAnswer) ? selectedAnswerRef : null}
                  >
                    <div>
                      <div
                        style={{ textAlign: "center", padding: 10, fontSize: "1.1em" }}
                      >
                        <b>
                          {answer.message}
                        </b>
                      </div>
                      <div style={{ justifyContent: "center", display: "flex", marginBottom: 10 }}>
                        {answer.image &&
                          <Avatar variant="rounded" src={answer.image.imageLink} alt={answer.image.alt} className={classes.preview} />}
                      </div>

                      <div
                        style={{ textAlign: "center", padding: 10, fontSize: "1.1em" }}
                      >


                        <Button
                          onClick={() => {
                            window.open("https://adventurepizza.github.io/Assemble/?profile=" + answer.owner);
                            enqueueSnackbar((answer.owner + " copied"), {
                              variant: "success",
                            });
                          }}
                        >

                          <b>
                            {answerOwnerDomains[index] ? answerOwnerDomains[index] :
                              answer.owner.slice(0, 6) + "..." + answer.owner.slice(32, 36)}

                          </b>
                        </Button>
                        <b>
                          received {answer.bounty} êœ©
                        </b>
                      </div>

                      <div style={{ justifyContent: "center", display: "flex", marginBottom: 10, position: "relative" }}>

                        {transactionWait === answer.timestamp ? <img src={loadingGif} alt="confirming transaction" height="40"></img> : (transactionWait === answer.timestamp * -1 ? <>Domo âœ…</> :

                          <Button
                            variant="contained"
                            className={classes.containedSuccess}
                            title={"domo"}
                            onClick={async () => {
                              // sent from activeAccount.address to answer.owner amount selectedRequest.prize
                              setTransactionWait(answer.timestamp);
                              dAppClient.requestOperation({
                                operationDetails: [
                                  {
                                    kind: TezosOperationType.TRANSACTION,
                                    destination: answer.owner, // Send to ourselves
                                    amount: (selectedRequest.prize * 1000000).toString(), // Amount in mutez, the smallest unit in Tezos
                                  },
                                ],
                              }).then(response => {
                                console.log(response);
                                setTransactionWait(answer.timestamp * -1);
                                setAnswer({
                                  ...answer,
                                  bounty: (answer.bounty ? answer.bounty : 0) + selectedRequest.prize
                                }, selectedRequest.timestamp)

                                setAnswers([
                                  ...answers.slice(0, index),
                                  {
                                    ...answer,
                                    bounty: (answer.bounty ? answer.bounty : 0) + selectedRequest.prize
                                  },
                                  ...answers.slice(index + 1)
                                ])

                                enqueueSnackbar("Domo âœ…", {
                                  variant: "success",
                                });

                              }).catch(e => {
                                console.log(e);
                                setTransactionWait(0);
                              });


                            }}
                            style={{ border: "solid 2px " }}
                          >
                            send a {selectedRequest.prize} êœ© gift ðŸ§§
                          </Button>

                        )}
                        <div style={{ position: "absolute", right: 0, bottom: 0 }}>
                          <Button
                            onClick={async () => {
                              console.log(window.location.href)

                              //share
                              navigator.clipboard.writeText(`https://asknetwork.xyz/?request=${selectedRequest.timestamp}&&answer=${answer.timestamp}`);
                              enqueueSnackbar("Link copied !", {
                                variant: "success",
                              });
                            }}
                          >
                            <img src={share} alt="share" width="25" />
                          </Button>
                        </div>
                      </div>

                    </div>
                  </Card>

                </div>
              ))}
          </div>
        </>
      }

      {view === "+request" &&
        <>
          <div style={{ fontSize: "0.9em", marginTop: 13, marginInline: 13 }}>
            <b>Add Request</b>
            <br></br>
            <br></br>
            <TextField
              id="outlined-basic"
              label="Request"
              variant="outlined"
              placeholder="recommend wallpaper"
              fullWidth
              onChange={handleRequestInput}
              value={requestInput}
            />
            <br></br>
            <br></br>
            <Button
              onClick={async () => {
                if (activeAccount && activeAccount.address) {
                  let timestamp = Date.now();
                  setRequest({
                    request: requestInput,
                    prize: parseFloat(prizeInput),
                    owner: activeAccount.address,
                    timestamp: timestamp,
                  })
                  setRequests(requests
                    .concat([
                      {
                        ...requests,
                        request: requestInput,
                        prize: parseFloat(prizeInput),
                        owner: activeAccount.address,
                        timestamp: timestamp,
                      },
                    ])
                    .sort((a, b) => parseFloat(b.timestamp) - parseFloat(a.timestamp)));

                  setSelectedRequest({
                    request: requestInput,
                    prize: parseFloat(prizeInput),
                    owner: activeAccount.address,
                    timestamp: timestamp
                  });
                  setView("answers")

                  socket.emit("request", {
                    request: requestInput,
                    prize: parseFloat(prizeInput),
                    owner: activeAccount.address,
                    timestamp: timestamp,
                  });
                }
              }}
              style={{ width: "100%", border: "solid 2px " }}
            >
              post
            </Button>
          </div>
        </>
      }
      {view === "+answer" &&
        <>
          <div style={{ fontSize: "0.9em", marginTop: 13, marginInline: 13 }}>
            <b>Add Answer</b>
            <br></br>
            <br></br>
            {selectedRequest.request}
            <br></br>
            <br></br>
            <div className="background-container" style={{ overflowY: 'auto' }}>
              <div className="background-icon-list" >
                {resultImages &&
                  resultImages.map(({ alt, thumbnailLink, imageLink, id }) => (
                    <IconButton
                      key={id}
                      onClick={() =>
                        //sendImage(imageLink, isSwitchChecked ? 'background' : 'image')
                        //<img src={thumbnailLink} alt={alt} width={"200"} />
                        setImage({ alt, thumbnailLink, imageLink, id })
                      }

                    >
                      <Avatar variant="rounded" src={thumbnailLink} alt={alt} className={classes.size} />
                    </IconButton>
                  ))
                }
              </div>
            </div>
            <br></br>
            <br></br>
            <div style={{ display: "flex", alignItems: "center" }}>
              <TextField
                id="outlined-basic"
                label="Search Unsplash"
                variant="outlined"
                placeholder="Sky"
                fullWidth
                onChange={handleImageInput}
                onKeyPress={onKeyPressImageSearch}
                value={imageInput}
              />
              <div style={{ margin: 10 }}>
                <Button
                  onClick={() => {
                    searchImages(imageInput)
                  }}

                >
                  search
                </Button>
              </div>
            </div>
            <br></br>
            <br></br>
            <TextField
              id="outlined-basic"
              label="Message"
              variant="outlined"
              placeholder="Light blue can be refreshing"
              fullWidth
              onChange={handleMessageInput}
              value={messageInput}
            />
            <br></br>
            <br></br>
            <div style={{ justifyContent: "center", display: "flex" }}>
              {image &&
                <Avatar variant="rounded" src={image.imageLink} alt={image.alt} className={classes.preview} />}
            </div>
            <br></br>
            <br></br>
            <Button
              onClick={async () => {
                if (activeAccount && activeAccount.address) {
                  let timestamp = Date.now();
                  setAnswer({
                    message: messageInput,
                    image: image,
                    owner: activeAccount.address,
                    timestamp: timestamp,
                    bounty: 0
                  }, selectedRequest.timestamp)

                  setAnswers(answers
                    .concat([
                      {
                        ...answers,
                        message: messageInput,
                        image: image,
                        owner: activeAccount.address,
                        timestamp: timestamp,
                        bounty: 0
                      },
                    ])
                    .sort((a, b) => parseFloat(b.timestamp) - parseFloat(a.timestamp)));
                  setView("answers")

                  let tempAnswers = answers
                    .concat([
                      {
                        ...answers,
                        message: messageInput,
                        image: image,
                        owner: activeAccount.address,
                        timestamp: timestamp,
                        bounty: 0
                      },
                    ]);

                  let tempTotalBounty = 0;
                  for (let i = 0; i < tempAnswers.length; i++) {
                    if (tempAnswers[i].bounty && tempAnswers[i].bounty > 0)
                      tempTotalBounty++;
                  }

                  setSelectedHoverText(`Responses ${tempAnswers.length} Rewards ${tempTotalBounty} Bounty ${tempTotalBounty * selectedRequest.prize}`)
                }
              }}
              style={{ width: "100%", border: "solid 2px " }}
            >
              post
            </Button>
          </div>
        </>

      }




    </div >
  );
}

export default App;
